import './style/main.css'
import Menu from "./Menu";
import React from "react";

const Golovna = () => {
    return (
        <div className="main">
            <Menu/>
            <div className='con'>
            <div className="vstup">
                Привіт, зібрався у відпустку? <br/> Тоді саме час заглянути на наш сайт.
                Ми обрали найкращі місця у світі де можна провести незабутню відпустку за рахунок фірми.
                Поки ти працював, тобі нараховувалися бали за старанну працю та досягнення в роботі.
            </div>
            </div>
        </div>
    );
};

export default Golovna;
