import React, {useState} from 'react';
import './style/modal.css'

const ModalAut = ({active, setActive, setLogName}) => {
    const [auth, setAuth] = useState(true)
    const [login, setLogin] = useState("");
    const [password, setPassword] = useState("");
    const requestData ={
        username: login,
        password: password
    }

    return (
        <div className={active ? "modal active" : "modal"} onClick={() => setActive(false)}>
            <div className={active ? "modal_content active" : "modal_content"} onClick={e => e.stopPropagation()}>
                <label className="auth_text">Введіть вашу пошту</label>
                <input value={login} onClick={() => setAuth(true)} onChange={(e) => setLogin(e.target.value)}
                       className={auth ? "auth_input" : "auth_input error"}
                       type="text"/>

                <label className="auth_text">Введіть ваш пароль</label>
                <input value={password} onClick={() => setAuth(true)} onChange={(e) => setPassword(e.target.value)}
                       className={auth ? "auth_input" : "auth_input error"} type="password"/>

                <label className={auth ? "auth_label" : "auth_label auth_label_active"}>*Невірний логін або
                    пароль</label>


                <button className="button auth_text" onClick={() => {
                    fetch(' http://voronadyplom.pp.ua/User/Token', {
                        method: 'POST',
                        headers: {
                            'Content-Type': 'application/json'
                        },
                        body: JSON.stringify(requestData),
                    }).then(r => {
                        r.json().then(data => {
                            if (data.errorText !== "Invalid username or password.") {
                                setActive(false);
                                sessionStorage.setItem('token', data.access_token);
                                sessionStorage.setItem('name', data.username);
                                setLogName(sessionStorage.getItem('name'));
                            } else {
                                setPassword("")
                                setLogin("")
                                setAuth(false)
                            }
                        })

                    })

                }}>Авторизуватися
                </button>

            </div>
        </div>
    );
};

export default ModalAut;
