import React, {useEffect, useRef, useState} from 'react';
import ModalAut from "./ModalAut";
import './style/menu.css'
import {Link,} from "react-router-dom";

const Menu = () => {
    const [logName, setLogName] = useState('')
    const [modalActive, setModalActive] = useState(false)
    const [changeState, setChangeState] = useState(false)
    const [activeLink, setActiveLink] = useState(false);
    const [pointInf, setPointInf] = useState('')

    let textRef = useRef(sessionStorage.getItem('name'))
    let state = useRef(changeState)
    const setActiveLinkRef = useRef(setActiveLink)


    useEffect(() => {
        if (textRef.current === null) {
            setLogName("LogIn")
        } else {
            setLogName(textRef.current);
        }
    }, [state.current])


    useEffect(()=> {
        fetch(' http://voronadyplom.pp.ua/User/IsLoggedIn', {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": `Bearer ${sessionStorage.getItem('token')}`
            }
        }).then(r => {
            r.json().then(data => data ? setActiveLinkRef.current(true) : setActiveLinkRef.current(false));
        })

        fetch(`http://voronadyplom.pp.ua/User/MyProfile/`, {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Authorization": `Bearer ${sessionStorage.getItem('token')}`
            }
        }).then(r => r.json().then(data => setPointInf(data)))
    },[logName])

    return (
        <div>
            <div className='zagolovok'>
                <ul className="menu small_text">
                    <li><Link to="/">Головна</Link></li>
                    <li><Link className={activeLink ? 'active_link' : 'disable_link'}  to={"/tour"}>Обрати подорож</Link></li>
                    <li><Link to="/point">Система балів</Link></li>
                </ul>
                ТОВ "Полюс Плюс"
                <button className="small_text log_bt"
                        onClick={() => {
                            setModalActive(true)
                            setChangeState(true)
                        }}
                        disabled={logName !== "LogIn"}>
                    {logName}
                </button>
                <div className={sessionStorage.getItem('name') === null ? 'alert_disable' : 'small_text'}>Ваші бали: {pointInf.points}</div>
            </div>

            <ModalAut
                setLogName={setLogName}
                active={modalActive}
                setActive={setModalActive}>
            </ModalAut>

        </div>
    );
};

export default Menu;
