import React, {useEffect, useState} from 'react';
import Menu from "./Menu";
import './style/main.css'
import './style/pointSystem.css'

const PointsSystem = () => {

    useEffect(() => {

    }, [])
    return (
        <div className='main'>
            <Menu/>
            <div className={'con'}>
                <div className="vstup">Поки ви працюєте вам нараховують бали за виконання вашого плану продаж за місяць.
                    Нижче в таблиці представлені які бали ви отримаєте за кожен відсоток плану:
                </div>
                <table className="vstup iksweb">
                    <tbody>
                    <tr>
                        <td>60-70%</td>
                        <td>не нараховуються</td>
                    </tr>
                    <tr>
                        <td>70-80%</td>
                        <td>2 бали</td>
                    </tr>
                    <tr>
                        <td>80-90%</td>
                        <td>3 бали</td>
                    </tr>
                    <tr>
                        <td>90-100%</td>
                        <td>4 бали</td>
                    </tr>
                    <tr>
                        <td>100-110%</td>
                        <td>5 балів</td>
                    </tr>
                    <tr>
                        <td>110%+</td>
                        <td>7 балів</td>
                    </tr>
                    </tbody>
                </table>
            </div>
        </div>
    );
};

export default PointsSystem;
