import React, {useEffect, useRef, useState} from 'react';
import {useParams} from 'react-router-dom'
import './style/tourCard.css'
import Menu from "./Menu";
import {logDOM} from "@testing-library/react";
const TourCart = ({ img, description, price}) => {
    const [active, setActive] = useState('true')
    const {tourId} = useParams()
    // console.log(tourId);
    const tourIdRef = useRef(tourId)
    const [tours, setTours] = useState({})
    const [loading, setLoading] = useState(false)
    useEffect(()=>{
        fetch(' http://voronadyplom.pp.ua/Tour/GetAll', {
            method: 'GET',
        }).then(r => r.json().then(data => {
            setTours(data.find((el)=> el.internalId == tourIdRef.current))
            setLoading(true)
        }))
    },[])

        function book(){
            fetch(`http://voronadyplom.pp.ua/Tour/Book?id=${tourId}`, {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json',
                    "Authorization": `Bearer ${sessionStorage.getItem('token')}`,
                    'id': tourId
                }
            }).then(r => {
                if (r.status === 200){
                    setActive(false)
                }
            })
        }

    return (
        <div className="main">
            <Menu/>
            <div className={loading ? 'alert_disable' : 'vstup'}>  Loading..  </div>
                <div className={loading ? 'active_loader' : 'alert_disable'}>
            <img className='image'  src={`../img/` + tours.picturePath} alt="pic"/>
            <div className='container_card'>
<div className='main_info_card'>
                <div className='name'>{tours.name}</div>
                <div className='text'>Коштує {tours.price} балів</div>
                <button className={active ? "button_book" : "alert_disable"} onClick={book}>Обрати тур</button>
                <div className={active ? 'alert_disable' : 'alert_active text'} >Тур заброньовано, бажаємо гарної дороги!</div>
</div>
                <div className='text'>{tours.description}</div>
            </div>
            </div>
            </div>

    );
};

export default TourCart;
