import React, {useEffect, useState} from 'react';
import Menu from "./Menu";
import "./style/changeTour.css"
import {Link} from "react-router-dom";
import {wait} from "@testing-library/user-event/dist/utils";

const ChangeTour = () => {
    const [arrayTours, setArrayTours] = useState([])
    const [loadPage, setLoadPage] = useState(false)

    useEffect( ()=>{
              fetch('http://voronadyplom.pp.ua/Tour/GetAffordable', {
               method: 'GET',
               headers: {
                   "Authorization": `Bearer ${sessionStorage.getItem('token')}`
               }
           }).then(r => r.json().then(data =>{
               setArrayTours(data) ;
                setLoadPage(true)
           }))


    },[])


    return (
        <div className="main">
            <Menu/>
            <div className={loadPage ? 'page_disable' : 'vstup'}>Loading..</div>
            <div className={loadPage ?  "container_change table_ref" : 'page_disable'}>

                <div className={arrayTours.length === 0 ? 'activeTour' : 'disableTour'}>Нажаль поки вам недоступно жодного туру</div>
               {arrayTours.map((e) =><Link to={`/tour/${e.internalId}`} onClick={() => {}}>  <div className="img_container">
                  <span className="text_on_image">{e.name}</span>
                  <img className="image_for_change" key={e.internalId} src={`./img/${e.picturePath}`} alt={e.name} /></div></Link>)}
            </div>
        </div>
    );
};

export default ChangeTour;
